import React, { useState } from 'react';
import axios from 'axios';

function ZapVoiceMaster({ chave, chatId }) {
  const [audioFile, setAudioFile] = useState(null);
  const [audioDelay, setAudioDelay] = useState(0);
  const [message, setMessage] = useState('');
  const [audioUrl, setAudioUrl] = useState('');

  const enviarAudio = async () => {
    if (!audioFile) {
      alert('Selecione um arquivo de udio.');
      return;
    }

    const formData = new FormData();
    formData.append('file', audioFile);
    formData.append('id', chatId);
    formData.append('userType', chatId.includes("@g.us") ? "group" : "user");
    formData.append('delay', audioDelay);

    try {
      const response = await axios.post(`https;//evolucaohot.online/message/audiofile?key=${chave}`, formData);
      alert('udio enviado com sucesso.');
    } catch (error) {
      console.error('Erro ao enviar udio:', error);
      alert('Erro ao enviar udio.');
    }
  };

  const transformarVoz = async () => {
    try {
      const response = await axios.post(`https;//evolucaohot.online/gerar-audio?key=${chave}`, { textoDoInput: message, tom: 'normal' }, { responseType: 'arraybuffer' });
      const blob = new Blob([response.data], { type: 'audio/mpeg' });
      setAudioUrl(URL.createObjectURL(blob));
    } catch (error) {
      console.error('Erro ao transformar voz:', error);
    }
  };

  const confirmarAudio = async () => {
    if (!audioUrl) {
      alert('No h udio para enviar.');
      return;
    }

    const audioResponse = await fetch(audioUrl);
    const audioBlob = await audioResponse.blob();
    const audioFile = new File([audioBlob], 'audiofile.mp3', { type: 'audio/mpeg' });

    const formData = new FormData();
    formData.append('file', audioFile);
    formData.append('id', chatId);
    formData.append('userType', chatId.includes("@g.us") ? "group" : "user");
    formData.append('delay', audioDelay);

    try {
      const response = await axios.post(`https;//evolucaohot.online/message/audiofile?key=${chave}`, formData);
      alert('udio enviado com sucesso.');
      setAudioUrl('');
    } catch (error) {
      console.error('Erro ao enviar udio:', error);
      alert('Erro ao enviar udio.');
    }
  };

  return (
    <div className="bg-gray-800 p-4 w-80 overflow-y-auto">
      <h2 className="text-xl font-bold mb-4">ZapVoice Master</h2>

      <div className="mb-4">
        <label className="block mb-2">Escolha um arquivo de udio:</label>
        <input type="file" accept="audio/*" onChange={(e) => setAudioFile(e.target.files[0])} className="w-full" />
      </div>

      <div className="mb-4">
        <label className="block mb-2">Atraso (em segundos):</label>
        <input type="number" value={audioDelay} onChange={(e) => setAudioDelay(e.target.value)} min="0" step="1" className="w-full bg-gray-700 text-white p-2 rounded" />
      </div>

      <button onClick={enviarAudio} className="bg-green-500 text-white p-2 rounded w-full mb-4">Enviar udio</button>

      <div className="mb-4">
        <label className="block mb-2">Mensagem para transformar em voz:</label>
        <textarea value={message} onChange={(e) => setMessage(e.target.value)} className="w-full bg-gray-700 text-white p-2 rounded" rows="3"></textarea>
      </div>

      <button onClick={transformarVoz} className="bg-blue-500 text-white p-2 rounded w-full mb-4">Transformar Voz</button>

      {audioUrl && (
        <div className="mb-4">
          <audio src={audioUrl} controls className="w-full mb-2"></audio>
          <button onClick={confirmarAudio} className="bg-green-500 text-white p-2 rounded w-full">Enviar udio Gerado</button>
        </div>
      )}
    </div>
  );
}

export default ZapVoiceMaster;