import React, { useState, useEffect, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { motion, AnimatePresence } from 'framer-motion';
import ChatList from './ChatList';
import ChatWindow from './ChatWindow';
import Header from './Header';
import ZapVoiceMaster from './ZapVoiceMaster';
import Funis from './Funis';

function WhatsAppInterface() {
  const { chave, nome } = useParams();
  const [chats, setChats] = useState([]);
  const [selectedChat, setSelectedChat] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const [showZapVoice, setShowZapVoice] = useState(false);
  const [showFunis, setShowFunis] = useState(false);

  const fetchChats = useCallback(async () => {
    try {
      const response = await axios.get(`https;//evolucaohot.online/instance/gchats?key=${chave}`);
      const sortedChats = response.data.sort((a, b) => {
        const aLastMessage = a.data.mensagens[a.data.mensagens.length - 1];
        const bLastMessage = b.data.mensagens[b.data.mensagens.length - 1];
        return new Date(bLastMessage.data) - new Date(aLastMessage.data);
      });

      setChats(prevChats => {
        // Verifica se h alteraes nos chats
        if (JSON.stringify(prevChats) !== JSON.stringify(sortedChats)) {
          return sortedChats;
        }
        return prevChats;
      });

      setLoading(false);
    } catch (error) {
      console.error('Error fetching chats:', error);
      setLoading(false);
    }
  }, [chave]);

  useEffect(() => {
    fetchChats();
    const intervalId = setInterval(fetchChats, 5000); // Atualiza a cada 5 segundos

    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    window.addEventListener('resize', handleResize);

    return () => {
      clearInterval(intervalId);
      window.removeEventListener('resize', handleResize);
    };
  }, [fetchChats]);

  useEffect(() => {
    if (selectedChat) {
      const updatedChat = chats.find(chat => chat.id === selectedChat.id);
      if (updatedChat) {
        setSelectedChat(updatedChat);
      }
    }
  }, [chats, selectedChat]);

  const handleChatSelect = (chat) => {
    setSelectedChat(chat);
    setShowZapVoice(false);
    setShowFunis(false);
  };

  const handleCloseChat = () => {
    setSelectedChat(null);
    setShowZapVoice(false);
    setShowFunis(false);
  };

  const sendMessage = async (message, chatId) => {
    try {
      await axios.post(`https;//evolucaohot.online/message/text?key=${chave}`, {
        id: chatId,
        typeId: 'user',
        message: message,
      });
      showSuccessPopup('Mensagem enviada');

      // Atualiza os chats imediatamente aps enviar a mensagem
      fetchChats();
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  const sendFile = async (file, chatId, type) => {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('id', chatId);
    formData.append('userType', 'user');

    try {
      await axios.post(`https;//evolucaohot.online/message/${type}?key=${chave}`, formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      showSuccessPopup(`${type.charAt(0).toUpperCase() + type.slice(1)} enviado`);
      fetchChats();
    } catch (error) {
      console.error(`Error sending ${type}:`, error);
    }
  };

  const showSuccessPopup = (message) => {
    const popup = document.createElement('div');
    popup.className = 'fixed top-4 right-4 bg-green-500 text-white py-2 px-4 rounded shadow-lg z-50';
    popup.textContent = message;
    document.body.appendChild(popup);
    setTimeout(() => {
      document.body.removeChild(popup);
    }, 1000);
  };

  const toggleZapVoice = () => {
    setShowZapVoice(!showZapVoice);
    setShowFunis(false);
  };

  const toggleFunis = () => {
    setShowFunis(!showFunis);
    setShowZapVoice(false);
  };

  const closePopup = () => {
    setShowZapVoice(false);
    setShowFunis(false);
  };

  return (
    <div className="flex flex-col h-screen bg-gray-900 text-white">
      <Header />
      <div className="flex flex-1 overflow-hidden">
        {(!isMobile || !selectedChat) && (
          <ChatList
            chats={chats}
            onSelectChat={handleChatSelect}
            loading={loading}
          />
        )}
        {selectedChat && (
          <ChatWindow
            chat={selectedChat}
            userName={nome}
            onClose={handleCloseChat}
            onSendMessage={sendMessage}
            onSendFile={sendFile}
            isMobile={isMobile}
            onToggleZapVoice={toggleZapVoice}
            onToggleFunis={toggleFunis}
          />
        )}
        <AnimatePresence>
          {(showZapVoice || showFunis) && (
            <motion.div
              initial={{ opacity: 0, y: "100%" }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: "100%" }}
              transition={{ type: "spring", stiffness: 300, damping: 30 }}
              className={`fixed inset-0 bg-gray-900 z-50 ${isMobile ? 'p-4' : 'p-8'}`}
            >
              <button
                onClick={closePopup}
                className="absolute top-4 right-4 text-white"
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
              {showZapVoice && <ZapVoiceMaster chave={chave} chatId={selectedChat.id} />}
              {showFunis && <Funis chave={chave} chatId={selectedChat.id} />}
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </div>
  );
}

export default WhatsAppInterface;