import React, { useState, useRef, useEffect } from 'react';

function ChatWindow({ chat, userName, onClose, onSendMessage, onSendFile, isMobile, onToggleZapVoice, onToggleFunis }) {
  const [message, setMessage] = useState('');
  const messagesEndRef = useRef(null);
  const [showOptions, setShowOptions] = useState(false);

  useEffect(() => {
    scrollToBottom();
  }, [chat]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const handleSendMessage = (e) => {
    e.preventDefault();
    if (message.trim()) {
      onSendMessage(message, chat.id);
      setMessage('');
    }
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const fileType = file.type.startsWith('image/') ? 'image' : 'document';
      onSendFile(file, chat.id, fileType);
    }
  };

  const handleAudioUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      onSendFile(file, chat.id, 'audio');
    }
  };

  const toggleOptions = () => {
    setShowOptions(!showOptions);
  };

  return (
    <div className={`flex flex-col ${isMobile ? 'w-full h-[calc(100vh-64px)]' : 'w-full h-[calc(100vh-64px)]'} bg-[#0a1014] mt-16`}>
      {/* Cabealho do chat */}
      <div className="bg-[#202c33] text-white py-3 px-4 flex justify-between items-center">
        <div className="flex items-center">
          {isMobile && (
            <button onClick={onClose} className="text-gray-300 mr-3">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
          )}
          <img
            src={chat.data.imagem}
            alt={chat.data.nome}
            className="w-10 h-10 rounded-full mr-3"
          />
          <div>
            <h2 className="font-medium text-lg">{chat.data.nome}</h2>
            <p className="text-sm text-gray-400">Online</p>
          </div>
        </div>
        <div className="flex items-center space-x-4">
          <button onClick={toggleOptions} className="text-gray-300">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" viewBox="0 0 20 20" fill="currentColor">
              <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z" />
            </svg>
          </button>
        </div>
      </div>

      {showOptions && (
        <div className="absolute right-4 top-32 w-48 rounded-md shadow-lg bg-[#2a3942] ring-1 ring-black ring-opacity-5 z-20">
          <div className="py-1" role="menu" aria-orientation="vertical" aria-labelledby="options-menu">
            <button
              onClick={onToggleZapVoice}
              className="block px-4 py-2 text-sm text-gray-300 hover:bg-[#3c4c59] w-full text-left"
              role="menuitem"
            >
              ZapVoice Master
            </button>
            <button
              onClick={onToggleFunis}
              className="block px-4 py-2 text-sm text-gray-300 hover:bg-[#3c4c59] w-full text-left"
              role="menuitem"
            >
              Funis
            </button>
          </div>
        </div>
      )}

      {/* rea de mensagens */}
      <div className="flex-1 overflow-y-auto p-4 bg-[#0a1014]">
        <div className={`max-w-3xl mx-auto ${isMobile ? 'w-full' : ''}`}>
          {chat.data.mensagens.map((msg, index) => (
            <div
              key={index}
              className={`mb-4 ${msg.user === userName ? 'flex justify-end' : 'flex justify-start'}`}
            >
              <div
                className={`max-w-[70%] p-3 rounded-lg ${msg.user === userName ? 'bg-[#005c4b] text-white' : 'bg-[#202c33] text-gray-200'
                  }`}
              >
                <p className="text-sm break-words">{msg.mensagem}</p>
                <p className="text-xs text-gray-400 mt-1 text-right">{msg.data}</p>
              </div>
            </div>
          ))}
          <div ref={messagesEndRef} />
        </div>
      </div>

      {/* rea de entrada de mensagem */}
      <div className="bg-[#202c33] py-3 px-4">
        <div className={`max-w-3xl mx-auto ${isMobile ? 'w-full' : ''}`}>
          <form onSubmit={handleSendMessage} className="flex items-center">
            <input
              type="text"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              placeholder="Digite uma mensagem"
              className="flex-1 bg-[#2a3942] text-white text-base border-none rounded-full py-2 px-4 mr-3 focus:outline-none focus:ring-2 focus:ring-[#00a884]"
            />
            <label className="cursor-pointer mr-3">
              <input type="file" className="hidden" onChange={handleFileUpload} />
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-[#8696a0]" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.172 7l-6.586 6.586a2 2 0 102.828 2.828l6.414-6.586a4 4 0 00-5.656-5.656l-6.415 6.585a6 6 0 108.486 8.486L20.5 13" />
              </svg>
            </label>
            <label className="cursor-pointer mr-3">
              <input type="file" accept="audio/*" className="hidden" onChange={handleAudioUpload} />
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-[#8696a0]" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
              </svg>
            </label>
            <button type="submit" className="bg-[#00a884] text-white rounded-full p-2">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
              </svg>
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default ChatWindow;