import React from 'react';
import { motion } from 'framer-motion';

function ChatList({ chats, onSelectChat, loading }) {
  if (loading) {
    return (
      <div className="w-full md:w-1/3 bg-gray-100 p-4 flex justify-center items-center">
        <div className="w-8 h-8 border-t-2 border-blue-500 rounded-full animate-spin"></div>
      </div>
    );
  }

  return (
    <div className="w-full md:w-1/3 bg-gray-100 overflow-y-auto">
      {chats.map((chat) => (
        <motion.div
          key={chat.id}
          className="flex items-center p-3 border-b border-gray-200 hover:bg-gray-200 cursor-pointer"
          onClick={() => onSelectChat(chat)}
          whileHover={{ backgroundColor: "#e5e7eb" }}
          whileTap={{ scale: 0.98 }}
        >
          <img
            src={chat.data.imagem}
            alt={chat.data.nome}
            className="w-10 h-10 rounded-full mr-3"
          />
          <div className="flex-1 min-w-0">
            <h2 className="font-medium text-gray-800 truncate">{chat.data.nome}</h2>
            <p className="text-sm text-gray-500 truncate">
              {chat.data.mensagens[chat.data.mensagens.length - 1].mensagem}
            </p>
          </div>
          <span className="text-xs text-gray-400">
            {new Date(chat.data.mensagens[chat.data.mensagens.length - 1].data).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
          </span>
        </motion.div>
      ))}
    </div>
  );
}

export default ChatList;