import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Funis({ chave, chatId }) {
  const [funisBoard, setFunisBoard] = useState([]);
  const [funisData, setFunisData] = useState([]);
  const [visuUnica, setVisuUnica] = useState(false);

  useEffect(() => {
    fetchFunis();
  }, []);

  const fetchFunis = async () => {
    try {
      const boardResponse = await axios.get(`https;//evolucaohot.online/api/funis/${chave}`);
      setFunisBoard(boardResponse.data);

      const dataResponse = await axios.get(`https;//evolucaohot.online/instance/displayallfunis?key=${chave}`);
      setFunisData(dataResponse.data);
    } catch (error) {
      console.error('Error fetching funis:', error);
    }
  };

  const showFunilName = async (funilName) => {
    try {
      await axios.get(`https;//evolucaohot.online/instance/sendfunil?key=${chave}&funil=${funilName}&chat=${chatId}&visuunica=${visuUnica}`);
      alert('Funil enviado!');
    } catch (error) {
      console.error('Error sending funil:', error);
    }
  };

  const showFunilName2 = async (funilName) => {
    try {
      await axios.get(`https;//evolucaohot.online/instance/sendfunil2?key=${chave}&funil=${funilName}&chat=${chatId}`);
      alert('Funil enviado!');
    } catch (error) {
      console.error('Error sending funil:', error);
    }
  };

  return (
    <div className="bg-gray-800 p-4 w-80 overflow-y-auto">
      <h2 className="text-xl font-bold mb-4">Funis</h2>

      <div className="mb-4">
        <label className="flex items-center">
          <input type="checkbox" checked={visuUnica} onChange={(e) => setVisuUnica(e.target.checked)} className="mr-2" />
          Visu nica
        </label>
      </div>

      <ul className="space-y-2">
        {funisBoard.map((funil, index) => (
          <li key={index} className="bg-gray-700 p-2 rounded">
            <button onClick={() => showFunilName2(funil.nome)} className="text-left w-full">
              {funil.nome}
            </button>
          </li>
        ))}

        {funisData.map((funil, index) => (
          <li key={index} className="bg-gray-700 p-2 rounded">
            <button onClick={() => showFunilName(funil.funilName)} className="text-left w-full">
              {funil.funilName}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Funis;